.top-container {
  width: 100%;
  height: 60px;
  position: relative;
}

.top-container .login-container {
  position: relative;
  z-index: 1;
  width: 100%;
  height: 60px;
  background-color: var(--second-color);
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
  -ms-flex-direction: column;
  flex-direction: column;
}

.top-container .login-container .login-ul {
  width: 100%;
  height: 100%;
  padding-right: 0px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: end;
  -ms-flex-pack: end;
  justify-content: center;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
}

.top-container .login-container .login-ul .login-li {
  height: 100%;
  width: 15%;
  color: var(--main-color);
  font-weight: bold;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  justify-content: center;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  font-size: 1em;
  text-transform: uppercase;
  -webkit-transition: 0.3s;
  transition: 0.3s;
  border-bottom: 3px solid var(--second-color);
}

.top-container .login-container .login-ul .login-li:hover {
  background-color: var(--main-color);
  color: var(--third-color);
  cursor: pointer;
}

.top-container .login-container .login-ul .login-li.active {
  background-color: var(--main-color);
  color: var(--third-color);
}

.top-container .inner-login-post {
  width: 100%;
  position: relative;
  top: 0;
  left: 0;
  -webkit-transition: 0.3s;
  transition: 0.3s;
  border-bottom: 3px solid var(--main-color);
}

.top-container .inner-login-post .login-post {
  width: 100%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
  -ms-flex-direction: column;
  flex-direction: column;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
}

.top-container .inner-login-post .login-post form {
  margin: 40px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
  -ms-flex-direction: column;
  flex-direction: column;
}

.top-container .inner-login-post .login-post form .post-h1 {
  font-size: 1.4em;
  margin-bottom: 5px;
  color: var(--main-color);
}

.top-container .inner-login-post .login-post form .post-h2 {
  font-size: 1.2em;
  font-weight: normal;
  text-transform: uppercase;
  margin-bottom: 30px;
}

.top-container .inner-login-post .login-post form label {
  margin-bottom: 5px;
}

.top-container .inner-login-post .login-post form .login-input {
  margin-bottom: 20px;
  height: 25px;
}

.top-container .inner-login-post .login-post form .button-login {
  height: 35px;
  text-transform: uppercase;
  font-size: 0.7em;
  border: none;
  background-color: var(--second-color);
  color: var(--main-color);
  border-radius: 5px;
  -webkit-transition: 0.3s;
  transition: 0.3s;
}

.top-container .inner-login-post .login-post form .button-login:hover {
  cursor: pointer;
  background-color: var(--fourth-color);
  color: white;
}

@media (min-width: 320px) and (max-width: 767px) {
  .top-container .login-container .login-ul .login-li {
    height: 100%;
    width: 40%;
    font-size: 0.8em;
  }
}

@media (min-width: 1025px) {
  .top-container .login-container .login-ul {
    padding-right: 100px;
    justify-content: flex-end;
  }
  .top-container .inner-login-post .login-post {
    -webkit-box-orient: horizontal;
    -webkit-box-direction: normal;
    -ms-flex-direction: row;
    flex-direction: row;
    -ms-flex-pack: distribute;
    justify-content: space-around;
  }
}
