/* burger */
.burger-wrapper{
    width: 80px;
    height: 80px;
    background-color: var(--main-color);
    border-radius: 50%;
    position: fixed;
    z-index: 10;
    right: 20px;
    top: 20px;
    cursor: pointer;

    /* kontener */
    display: flex;
    justify-content: center;
    align-items: center;
}

.burger-wrapper .burger{
    width: 80%;
    height: 50%;
    position: relative;

    /* kontener */
    display: flex;
    justify-content: center;
}

.burger-wrapper .burger span{
    display: block;
    position: absolute;
    width: 80%;
    height: 6px;
    background-color: #fff;
    border-radius: 50%;
    transform: translateY(-50%) rotate(0deg);
    transition: all .2s, transform .2s .2s;
}

.burger-wrapper .burger span:nth-child(1){
    top: 0;
}

.burger-wrapper .burger span:nth-child(2){
    top: 50%;
    opacity: 1;
}

.burger-wrapper .burger span:nth-child(3){
    bottom: 0;
    transform: translateY(50%);
}

.burger-wrapper.active .burger span:nth-child(1){
    top: 50%;
    transform: translateY(-50%) rotate(45deg);
}

.burger-wrapper.active .burger span:nth-child(2){
    opacity: 0;
    transform: translateY(-50%) rotate(0deg);
}

.burger-wrapper.active .burger span:nth-child(3){
    bottom: 50%;
    transform: translateY(50%) rotate(-45deg);
}

/* navbar */

.navbar-container{
    padding-top: 0px;
}

nav.navbar{
    height: 100vh;
    width: 100%;
    background-color: rgba(0, 0,0,0.85);
    position: fixed;
    transform: translateX(-100%);
    top: 0;
    left: 0;
    z-index: 5;
    transition: .4s;
}

nav.navbar.active{
    transform: translate(0);
}

.nav-ul{
    width: 100%;
    height: 100vh;
    list-style: none;

    display: flex;
    flex-direction: column;
    align-items: center;
}

.nav-a span{
    display: block;
    position: absolute;
    bottom: 10px;
    left: 50%;
    transform: translateX(-50%);
    height: 4px;
    width: 0;
    background-color: var(--main-color);
    transition: .3s;
}

.nav-a.active  span{
    width: 60%;
}

.nav-a.active{
    color: var(--main-color);
}

.nav-a:hover{
    color: var(--main-color);
}


.nav-a{
    width: 100%;
    text-decoration: none;
    flex-grow: 1;
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    color: white;
    font-size: 2em;
    text-transform: uppercase;
    
}

.nav-a:hover .nav-li{
    color: var(--main-color);
}


/* header */
.header{
    position: relative;
}


@media(min-width: 1025px){

    .burger-wrapper{
        visibility: hidden;
    }

    .navbar-container{
        padding-top: 60px;
    }

    nav.navbar{
        position: absolute;
        transform: translateX(0);
        height: 60px;
        top: 60px;
        background-color: var(--main-color);
    }

    .nav-a{
        font-size: 1.2em;
        flex-grow: 0;
        width: 180px;
        height: 60px;
        color: var(--second-color);
    }

    .nav-a span{
        height: 2px;
    }

    .nav-a.active, .nav-a:hover{
        color: white;
    }

    .nav-a.active span{
        background-color: white;
    }

    .nav-ul{
        height: 60px;
        flex-direction: row;
    }

}
