*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

:root {
    --main-color: #CC313D;
    --second-color: #fddce0;
    --third-color: #F7C5CC;
    --fourth-color: #ea6ab2;
  }

body{
    font-family: 'Lato', sans-serif;
}
.wrapper{
    width: 100%;
    position: relative;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
}

.top-container {
  width: 100%;
  height: 60px;
  position: relative;
}

.top-container .login-container {
  position: relative;
  z-index: 1;
  width: 100%;
  height: 60px;
  background-color: var(--second-color);
  display: flex;
  flex-direction: column;
}

.top-container .login-container .login-ul {
  width: 100%;
  height: 100%;
  padding-right: 0px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.top-container .login-container .login-ul .login-li {
  height: 100%;
  width: 15%;
  color: var(--main-color);
  font-weight: bold;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1em;
  text-transform: uppercase;
  transition: 0.3s;
  border-bottom: 3px solid var(--second-color);
}

.top-container .login-container .login-ul .login-li:hover {
  background-color: var(--main-color);
  color: var(--third-color);
  cursor: pointer;
}

.top-container .login-container .login-ul .login-li.active {
  background-color: var(--main-color);
  color: var(--third-color);
}

.top-container .inner-login-post {
  width: 100%;
  position: relative;
  top: 0;
  left: 0;
  transition: 0.3s;
  border-bottom: 3px solid var(--main-color);
}

.top-container .inner-login-post .login-post {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.top-container .inner-login-post .login-post form {
  margin: 40px;
  display: flex;
  flex-direction: column;
}

.top-container .inner-login-post .login-post form .post-h1 {
  font-size: 1.4em;
  margin-bottom: 5px;
  color: var(--main-color);
}

.top-container .inner-login-post .login-post form .post-h2 {
  font-size: 1.2em;
  font-weight: normal;
  text-transform: uppercase;
  margin-bottom: 30px;
}

.top-container .inner-login-post .login-post form label {
  margin-bottom: 5px;
}

.top-container .inner-login-post .login-post form .login-input {
  margin-bottom: 20px;
  height: 25px;
}

.top-container .inner-login-post .login-post form .button-login {
  height: 35px;
  text-transform: uppercase;
  font-size: 0.7em;
  border: none;
  background-color: var(--second-color);
  color: var(--main-color);
  border-radius: 5px;
  transition: 0.3s;
}

.top-container .inner-login-post .login-post form .button-login:hover {
  cursor: pointer;
  background-color: var(--fourth-color);
  color: white;
}

@media (min-width: 320px) and (max-width: 767px) {
  .top-container .login-container .login-ul .login-li {
    height: 100%;
    width: 40%;
    font-size: 0.8em;
  }
}

@media (min-width: 1025px) {
  .top-container .login-container .login-ul {
    padding-right: 100px;
    justify-content: flex-end;
  }
  .top-container .inner-login-post .login-post {
    flex-direction: row;
    justify-content: space-around;
  }
}

.bottom-container{
    flex-grow: 1;
    display: flex;
    flex-direction: column;
    transition: .3s;
}
/* burger */
.burger-wrapper{
    width: 80px;
    height: 80px;
    background-color: var(--main-color);
    border-radius: 50%;
    position: fixed;
    z-index: 10;
    right: 20px;
    top: 20px;
    cursor: pointer;

    /* kontener */
    display: flex;
    justify-content: center;
    align-items: center;
}

.burger-wrapper .burger{
    width: 80%;
    height: 50%;
    position: relative;

    /* kontener */
    display: flex;
    justify-content: center;
}

.burger-wrapper .burger span{
    display: block;
    position: absolute;
    width: 80%;
    height: 6px;
    background-color: #fff;
    border-radius: 50%;
    -webkit-transform: translateY(-50%) rotate(0deg);
            transform: translateY(-50%) rotate(0deg);
    transition: all .2s, -webkit-transform .2s .2s;
    transition: all .2s, transform .2s .2s;
    transition: all .2s, transform .2s .2s, -webkit-transform .2s .2s;
}

.burger-wrapper .burger span:nth-child(1){
    top: 0;
}

.burger-wrapper .burger span:nth-child(2){
    top: 50%;
    opacity: 1;
}

.burger-wrapper .burger span:nth-child(3){
    bottom: 0;
    -webkit-transform: translateY(50%);
            transform: translateY(50%);
}

.burger-wrapper.active .burger span:nth-child(1){
    top: 50%;
    -webkit-transform: translateY(-50%) rotate(45deg);
            transform: translateY(-50%) rotate(45deg);
}

.burger-wrapper.active .burger span:nth-child(2){
    opacity: 0;
    -webkit-transform: translateY(-50%) rotate(0deg);
            transform: translateY(-50%) rotate(0deg);
}

.burger-wrapper.active .burger span:nth-child(3){
    bottom: 50%;
    -webkit-transform: translateY(50%) rotate(-45deg);
            transform: translateY(50%) rotate(-45deg);
}

/* navbar */

.navbar-container{
    padding-top: 0px;
}

nav.navbar{
    height: 100vh;
    width: 100%;
    background-color: rgba(0, 0,0,0.85);
    position: fixed;
    -webkit-transform: translateX(-100%);
            transform: translateX(-100%);
    top: 0;
    left: 0;
    z-index: 5;
    transition: .4s;
}

nav.navbar.active{
    -webkit-transform: translate(0);
            transform: translate(0);
}

.nav-ul{
    width: 100%;
    height: 100vh;
    list-style: none;

    display: flex;
    flex-direction: column;
    align-items: center;
}

.nav-a span{
    display: block;
    position: absolute;
    bottom: 10px;
    left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    height: 4px;
    width: 0;
    background-color: var(--main-color);
    transition: .3s;
}

.nav-a.active  span{
    width: 60%;
}

.nav-a.active{
    color: var(--main-color);
}

.nav-a:hover{
    color: var(--main-color);
}


.nav-a{
    width: 100%;
    text-decoration: none;
    flex-grow: 1;
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    color: white;
    font-size: 2em;
    text-transform: uppercase;
    
}

.nav-a:hover .nav-li{
    color: var(--main-color);
}


/* header */
.header{
    position: relative;
}


@media(min-width: 1025px){

    .burger-wrapper{
        visibility: hidden;
    }

    .navbar-container{
        padding-top: 60px;
    }

    nav.navbar{
        position: absolute;
        -webkit-transform: translateX(0);
                transform: translateX(0);
        height: 60px;
        top: 60px;
        background-color: var(--main-color);
    }

    .nav-a{
        font-size: 1.2em;
        flex-grow: 0;
        width: 180px;
        height: 60px;
        color: var(--second-color);
    }

    .nav-a span{
        height: 2px;
    }

    .nav-a.active, .nav-a:hover{
        color: white;
    }

    .nav-a.active span{
        background-color: white;
    }

    .nav-ul{
        height: 60px;
        flex-direction: row;
    }

}

.home{
    font-size: 3em;
}
.shop{
    width: 100%;
    display: flex;
    flex-grow: 1;
}

/* SEARCH */

