    max-width: 350px;
    flex-grow: 1;
    background-color: rgb(245, 243, 243); 
    padding: 20px 30px;
    border-right: 3px solid var(--second-color);
}

.left-side .shop-tag{
    border-bottom: 4px solid var(--third-color);
    text-transform: uppercase;
    color: var(--main-color);
    text-align: center;
    padding: 15px 0;
    margin-top: 20px;
}

.left-side .shop-producer h4{
    margin-top: 10px;
}

.shop-producer .shop-combobox{
    width: 50%;
    height: 30px;
    margin-top: 10px;
    color: var(--main-color);
}

.shop-ul{
    list-style: none;
    padding-top: 10px;
}

.shop-li{
    padding: 5px 0;
}

.shop-li:hover{
    color: var(--main-color);
    cursor: pointer;
}


/* ITEMS */

.right-side{
    flex-grow: 1;
    padding: 20px 30px;
}
main.content{
    flex-grow: 1;
    display: flex;
    flex-direction: column;
}
footer.footer{
    width: 100%;
    height: 80px;
    background-color: #222;
    display: flex;
    justify-content: center;
    align-items: center;
    color: white;
    text-transform: uppercase;
    letter-spacing: 3px;
}
